body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: rgb(171, 197, 195);
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
/*here we give the dimension to the app*/
.App{
    width: 99vw;
    height: 99vh;



  }

  /*Navbar position button*/

  .btn-position{
    margin-left: 20%;
    width: 80%;
    margin-left: none;
    height: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }



  .grp-name{
    margin-top: 20px;
    font-style: Cursive;
    font-weight: 500;
    font-size: 20px;
  }

  /*css of the all avilable button*/

  .btn{
    height: 60%;
    margin-top: 1.1%;
    width: 8%;
    border: 3px solid grey;
    border-radius: 50px;
    background: rgba(255, 255, 255,0.2);
  }


  /*css of the hover button container*/

  #button-container{
    width: 40%;
    height: 12%;
    margin-left: 38%;
    margin-top: 25%;

  }


  /*special feature added in the hover button*/

  .hover-btn{
    height: 100%;
    width: 100%;
    font-size: 2rem;
    background-color: aliceblue;

  }

  /*css for the top position hover*/

  .top{
    height: 30%;
    width: 50%;
    margin-top: -50%;
    margin-left: 20%;
    padding: 6px 10px 15px 10px;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 1rem;
    background-color: rgba(104, 23, 227, 0.1);

  }


  /*css for the right position hover*/


  .right{
      height: 30%;
      width: 50%;
      margin-top:-22%;
      margin-left: 105%;
      padding: 6px 10px 15px 10px;
      border: 1px solid black;
      border-radius: 10px;
      font-size: 1rem;
      background-color: rgba(104, 23, 227, 0.1);
  }

/*css for the down position hover*/

  .down{
    height: 30%;
    width: 50%;
    margin-top: 5%;
    margin-left: 20%;
    padding: 6px 10px 15px 10px;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 1rem;
    background-color: rgba(104, 23, 227, 0.1);
  }


/*csss for the left position hover*/

  .left{
    height: 30%;
    width: 50%;
    margin-top:-22%;
    margin-left: -60%;
    padding: 6px 10px 15px 10px;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 1rem;
    background-color: rgba(104, 23, 227, 0.1);
  }
  .active{
    background-color:rgba(104, 23, 227, 0.3) ;
  }
